@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  overflow-x: hidden;
}

.main-module {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  font-family: sans-serif, "Roboto";
}
.main-module-image__container {
  width: 100%;
  height: 50%;
}
.main-module-content__container {
  width: 100%;
  height: 50%;
  padding-top: 46px;
}

.content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.content h1 {
  font-size: 42px;
  line-height: 50.4px;
  font-weight: 700;
}
.content p {
  max-width: 600px;
  padding: 32px;
  line-height: 24px;
}

.displayed-picture {
  width: 100%;
  height: 100%;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

@media (min-width: 768px) {
  .main-module-image__container {
    display: flex;
    justify-content: center;
    padding-top: 96px;
  }
  .content p {
    max-width: 720px;
  }
  .displayed-picture {
    width: auto;
  }
}
@media (min-width: 1140px) {
  .main-module {
    flex-direction: initial;
    max-width: 1440px;
  }
  .main-module-image__container {
    justify-content: flex-end;
    width: 50%;
  }
  .main-module-content__container {
    width: 50%;
    padding-top: 136px;
    padding-left: 95px;
  }
  .displayed-picture {
    width: 505px;
    height: 330px;
  }
  .content {
    width: auto;
    max-width: 380px;
    align-items: initial;
  }
  .content h1 {
    font-size: 42px;
    line-height: 50.4px;
    padding-bottom: 24px;
  }
  .content p {
    font-size: 16px;
    padding: 0;
    padding-bottom: 32px;
    line-height: 24px;
  }
}
.overlay-container {
  position: fixed;
  z-index: 5;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.overlay-container__backdrop {
  width: 100%;
  height: 100vh;
  position: fixed;
  background-color: #121212;
  opacity: 0.5;
}

.message-box__container {
  position: relative;
  z-index: 7;
  background-color: #FFFFFF;
  overflow: hidden;
  font-family: sans-serif, "Roboto";
}

.close-burger {
  position: absolute;
  right: 46px;
  top: 46px;
  width: 12px;
  height: 12px;
  color: #000000;
  cursor: pointer;
}
.close-burger:before, .close-burger:after {
  content: "";
  position: absolute;
  left: 5px;
  height: 12px;
  width: 2px;
  background-color: #000000;
}
.close-burger:before {
  transform: rotate(45deg);
  height: 17px;
}
.close-burger:after {
  transform: rotate(-45deg);
  height: 17px;
}

.message-box__content {
  display: flex;
  justify-content: space-between;
  align-items: self-end;
  padding: 57px 25px;
}

.resetButtonVisible {
  display: block !important;
}

.message-box__text {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.message-box__text h2 {
  width: 100%;
  max-width: 380px;
  color: #121212;
  font-size: 42px;
}
.message-box__text p {
  color: #121212;
  line-height: 24px;
}
.message-box__text span {
  font-weight: 600;
}

@media (min-width: 768px) {
  .message-box__container {
    width: 620px;
  }
}
@media (min-width: 1140px) {
  .message-box__container {
    width: 920px;
  }
  .message-box__content {
    padding-left: 95px;
    padding-right: 95px;
  }
}
.btn {
  word-break: normal;
  text-decoration: none;
  padding: 16px 40px;
  display: inline-block;
  border-radius: 48px;
  border: none;
  text-align: center;
  transition: all 0.2s;
  position: relative;
  font-weight: 700;
  cursor: pointer;
}

.btn-not-visible {
  display: none;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn-colors {
  background-color: #E4C1AE;
  color: #FFFFFF;
}

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
}

.btn-colors::after {
  background-color: #A0887C;
}

.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}